// src/pages/vizualisation.js (ou .tsx)

import PostCard, { Comment as PostCardComment } from "@/components/PostCard";
// -------------------------------------------------------------------------
// 1. Importez le fichier JSON (ajustez le chemin si nécessaire)
// Assurez-vous que le chemin est correct par rapport à l'emplacement de ce fichier
import mockPostsData from "../../data/mock_posts.json"; 
// -------------------------------------------------------------------------

// Vous n'avez plus besoin de définir 'mockComments' séparément si vous utilisez le tableau complet.

export default function Vizualisation() { // Majuscule pour le nom du composant
    
    return (
        <div style={{ maxWidth: '600px', margin: '0 auto', padding: '20px' }}>
            <h2>Flux de Publications (Mock Data)</h2>
            
            {/* ----------------------------------------------------------- */}
            {/* 2. Itérer sur le tableau de données (mockPostsData) */}
            {mockPostsData.map((post, index) => (
                // 3. Utilisez l'opérateur de propagation (...) pour passer toutes les propriétés du post
                // Ceci rend le code beaucoup plus propre et utilise directement l'objet JSON
                <PostCard
                    key={index} // Utilisez un index simple comme clé pour les données de maquette
                    {...post} // Passe toutes les propriétés (companyName, postTime, introText, likes, comments, etc.)
                    
                    // Note: Vous pouvez ajouter des fonctions de callback ici si nécessaire
                    onLike={() => console.log(`Post ${index} liked!`)}
                    onComment={() => console.log(`Commentaire sur Post ${index}`)}
                    onAddComment={(text) => console.log(`Nouveau commentaire: ${text}`)}
                />
            ))}
            {/* ----------------------------------------------------------- */}

        </div>
    );
}